
.record_input {
    width: 100%;
    height: 30px;
    font-size: 1rem;
    text-align: center;
    line-height: 20px;
    border: 5px groove;
}
.record_input3 {
    width: 21%;
    height: 30px;
    font-size: 1rem;
    text-align: center;
    line-height: 20px;
    border: 5px groove cornflowerblue;
}
.record_list_model:nth-child(2n+1) input {
    border: 5px groove #289ed4;
}
.add_record_btn {
    border-top: 2px solid;
    padding-top: 5px;
}
.record_btn_style {
    width: 49%;
    float: left;
    text-decoration: none;
    background: indianred;
    margin-top: 2px;
    padding: 0;
    height: 40px;
    line-height: 40px;
    font-size: 1.8rem;
}
.noDe {
    text-decoration: none;
    color: black;
}



