
html,
body,
div,
button,
ul,
li {
  font-size: 100%;
  margin: 0;
  padding: 0;
  text-align: center;
  background-color: #C7EDCC;
  border: 0;
}
h1 {
  font-weight: normal;
  font-size: 2.0rem;
  /*background-color: white;
  border-bottom: 1px solid grey;*/
  padding-bottom: 10px;
  text-shadow: 0px 0px 1px red;
  height: 40px;
}
.normal-btn {
  min-width: 120px;
  height: 60px;
  background-color: #09bb07;
  padding: 0 30px;
  border: 0;
  border-radius: 6px;
  font-size: 13pt;
  color: white;
}



