
.mychart {
    margin-bottom: 60px;
}
.chart-ctn {
    width: 96%;
    height: 400px;
    margin: 10px auto;
    border: 8px groove cornflowerblue;
    border-radius: 5px;
}
.data-area {
    max-height: 160px;
    margin: 20px 10px;
    border-bottom: 2px solid #888888;
    overflow-y: auto;
}
.data-item {
    margin-bottom: 10px;
}
.data-input {
    width: 60px;
    border-radius: 60px;
}
.btn-ctn {
    position: fixed;
    width: 100%;
    bottom: 0px;
    margin: 0 auto;
}



